/* Basic styling */
.app {
    display: flex !important;
    flex-direction: column !important;
    min-height: auto !important;
    background: linear-gradient(150deg, #fff0dc 0, #ffcdbc);
}

.app-container {
    max-width: 1536px !important; /* Increase the max-width */
    margin: 0 auto !important; 
    padding: 2rem !important;
    min-height: 88vh !important;
    box-sizing: border-box !important; /* Add this line */
}


.main-content {
    display: flex !important;
    max-width: 1536px !important;
    flex-wrap: nowrap !important;
    align-items: flex-start !important; /* Add this line */
}


.left-section {
    flex: 1 !important; /* Change this line */
    margin-right: 16px !important;
    min-width: 250px !important;
}


.right-section {
    flex: 1 !important;
    box-sizing: border-box !important;
    position: -webkit-sticky; /* Add this line for Safari */
    position: sticky !important;
    top: 2rem !important; /* Update the top value */
}


/* Add this media query for mobile layout */
@media (max-width: 600px) {
  .main-content {
    flex-direction: column;
  }
  
  .left-section, .right-section {
    flex: 1;
    max-width: 100%;
  }

  .left-section {
    margin-right: 0;
  }
}


.footer {
    position: auto !important;
    bottom: 0;
    width: 100%;
    height: 0.5rem !important;
    background-color: transparent !important;
    display: flex;
    justify-content: center;
    align-items: center;
}



h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: "Poppins", sans-serif !important;
    
}




/* AppBar */



.appbar {
    height: 8vh !important;
    display: flex !important;
    align-items: flex-start;
    background: rgba(255, 255, 255, 0) !important;
    font-weight: 500 !important;
    font-size: smaller !important;
    color: #89735e;
}

.header-appbar {
    font-weight: bold !important;
}




/* Header and heading */
.header {
    background-color: transparent !important;
    opacity: 20 !important;
    min-height: 10vh !important;
    text-align: left !important;
}

.heading {
    margin-bottom: 2rem !important;
    text-align: center;
    font-weight: bold !important;
    color: #302f2c;
}

.title-switch-container {
    display: flex;
    align-items: center !important;
    gap: 4vh;
}

.length-slider {
    min-width: 20rem !important;
    max-width: 400px !important;
}

.slider-value {
    text-align: left;
    font-weight: bold;
    margin-bottom: 8px;
    font-family: "Poppins", sans-serif !important;
}



/* Input field */
.my-input-field {
    border: 1px solid #ccc;
    border-radius: 4px !important;
    padding: 6px;
    font-size: 16px;
    font-weight: bold;
    width: 20rem;
    margin-right: 20rem;
    margin-bottom: 0.25rem;
    opacity: 80%;
    background-color: rgba(255, 255, 255, 0.662);
}

.my-input-field-main{
    border: 1px solid #ccc;
    border-radius: 4px !important;
    padding: 6px;
    font-size: 16px;
    font-weight: bold;
    width: 20rem;
    margin-right: 20rem;
    margin-bottom: 0.25rem;
    opacity: 80%;
    background-color: rgba(255, 255, 255, 0.662);
}
/* Buttons */

.language-switch .MuiSwitch-switchBase.Mui-checked {
    color: #0077c8 !important;
}

.language-switch .MuiSwitch-track.Mui-checked {
    background-color: #c18686 !important;
}

.language-switch .MuiSwitch-switchBase:not(.Mui-checked) {
    color: #c15656 !important;
}

.language-switch .MuiSwitch-track {
    background-color: #88b3d0 !important;
}


.language-select {
    display: flex;
    justify-content: left;
    align-items: center;
    margin-bottom: 16px;
}




.button-container {
    display: inline-block;
    margin-left: 0rem !important;
    padding: 4px 8px !important;
    font-size: 12px !important;
    font-family: "Poppins", sans-serif !important;

}

.button-deselected {
    border: 1px solid #967a6b !important;
    color: #1d1714 !important;
}

.button-selected {
    color: #ffffff !important;
    background-color: #caa490 !important;
}

/* Summary and clipboard */
.prompt-title {
    font-weight: bold !important;
    font-size: 28px !important;
    margin-top: 3rem !important;
}

.summary-options-container {
    padding: 0.5rem;
    font-weight: bold !important;
}

.btn-clipboard {
    margin: 1rem !important;
    padding: 8px 24px !important;
    border: 1px solid #f2ccac !important;
    color: white !important;
    opacity: 80%;
    font-size: 16px !important;
    background: linear-gradient(60deg, #a08670, #b4957d, #7e6a58);
    background-size: 400% 400%;
    animation: animatedgradient 6s ease infinite;
    cursor: pointer;
}

.btn-generate {
    margin: 1rem !important;
    padding: 8px 24px !important;
    border: 1px solid #f2ccac !important;
    color: white !important;
    opacity: 80%;
    font-size: 16px !important;
    background: linear-gradient(60deg, #a07070be, #b47d9e, #68587e);
    background-size: 400% 400%;
    animation: animatedgradient 6s ease infinite;
    cursor: pointer;
}

/* Titles and rows */
.row-title {
    padding-left: 8px;
    margin-top: 0.5rem !important;
}

.row-container {
    margin: 1rem;
    margin-left: 0rem !important;
}

.prompt-output {
    font-size: 20px !important;
    font-weight: bold !important;
    color: #635046;
}

.summary-container {
    margin: 1rem;
    margin-left: 0rem !important;
}

/* Text */
/*
.h4 {}

.h5 {}

.h6 {}
*/

/* Gradient animation */
@keyframes animatedgradient {
    0% {
        background-position: 0% 50%;
    }

    50% {
        background-position: 100% 50%;
    }

    100% {
        background-position: 0% 50%;
    }
}



@media (max-width: 600px) {
    .length-slider {
        min-width: 100% !important;
        max-width: 100% !important;
    }

    .my-input-field {
        width: 100%;
        margin-right: 0;
    }
}


.generated-text-container {
    padding: 1rem;
    max-width: 800px !important;
    min-height: calc(100vh - 50vh - 8rem - 2rem); /* Modify this line */
}

.generated-text {
    color: black !important;
    font-family: Arial, Helvetica, sans-serif !important;
    padding: 2rem;
    max-width: 800 px !important;
    min-height: calc(100vh - 50vh - 8rem - 2rem); /* Modify this line */
    border: 1px solid #ccc;
    border-radius: 4px !important;
    font-size: 16px;
    font-weight: bold;
    opacity: 70%;
    background-color: white;
}